.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
}
.header-logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
  height: 50px;
  margin-bottom: 10px;
}
.header-search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
}
.header-searchinput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}
.header-searchicon {
  padding: 5px;
  height: 22px !important;
  background-color: #cd9042;
}
.header-nav {
  display: flex;
  justify-content: space-evenly;
}
.header-option {
  display: flex;
  justify-content: space-evenly;
}
.header-optionone {
  font-size: 10px;
}
.header-optiontwo {
  font-size: 13px;
  font-weight: 800;
}
.header-option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}
.header-backeticon {
  display: flex;
  color: white;
  align-items: center;
}
.header-basketcount {
  margin-left: 10px;
  margin-right: 20px;
}
